import { SessionStateService } from '../components/SessionStateService';
import { ContextStateService } from './../components/ContextStateService';

export class ConfigHelper {

    /**
     * Constructeur de la classe ConfigHelper.
     * @param {Object} config - Configuration des oprations CRUD.
     */
    constructor(config, contextState,sessionState) {

        this.contextStateService = new ContextStateService(contextState);
        this.sessionStatService = new SessionStateService(sessionState);
        
        this.page = config.page || "index";
        
        this.entity_name = config.entity_name;
        this.edit_has_many = config.edit_has_many; // permet d'diter l'entity avec ses objet has many
        this.isMany = config.isMany;
        this.crudSelector = config.crudSelector;
        this.tableSelector = config.tableSelector;
        this.formSelector = config.formSelector;
        this.modalSelector = config.modalSelector;
        this.filterFormSelector = config.filterFormSelector;

        this.indexUrl = config.indexUrl; // URL pour la liste des entits
        this.createUrl = config.createUrl; // URL pour crer une nouvelle entit
        this.showUrl = config.showUrl; // URL pour afficher une entit
        this.editUrl = config.editUrl; // URL pour modifier une entit
        this.storeUrl = config.storeUrl; // URL pour enregistrer une nouvelle entit
        this.deleteUrl = config.deleteUrl; // URL pour supprimer une entit
        this.csrfToken = config.csrfToken; // Token CSRF pour les requtes scurises

        this.createTitle = config.create_title; // Titre pour le formulaire d'ajout
        this.editTitle = config.edit_title; // Titre pour le formulaire de modification

        this.searchInputSelector = `${this.crudSelector} .crud-search-input`;
        this.paginationSelector = `${this.crudSelector} .pagination`;
        this.dataContainerSelector = `${this.tableSelector}`;
        this.filterIconSelector = `${ config.filterFormSelector} .filter-icon`;
        this.sortableColumnSelector = `${ config.tableSelector} .sortable-column`;


        this.id = `${this.entity_name}-${this.page}`;
    }

    /**
     * Rcupre une URL configure avec un ID (ex : pour diter ou afficher une entit spcifique).
     * @param {string} url - URL de base (ex : editUrl ou showUrl).
     * @param {string|number} id - Identifiant de l'entit.
     * @returns {string} - URL formate avec l'ID.
     */
    getUrlWithId(url, id) {
        return url.replace(':id', id);
    }

    /**
     * Vrifie si la configuration est valide et complte.
     * @returns {boolean} - Retourne `true` si la configuration est valide.
     */
    isValid() {
        const requiredFields = [
            'crudSelector',
            'indexUrl',
            'createUrl',
            'showUrl',
            'editUrl',
            'storeUrl',
            'deleteUrl',
            'csrfToken',
            'entityName',
            'createTitle',
            'editTitle',
        ];

        return requiredFields.every((field) => this[field] !== undefined && this[field] !== null);
    }
}
