import Swal from 'sweetalert2';

export class NotificationHandler {


    constructor ({type,message}){
        this.type = type;
        this.message = message;
    }

    show(){
        switch (this.type) {
            case "info": NotificationHandler.showInfo(this.message);break;
            case "success": NotificationHandler.showSuccess(this.message);break;
            case "error": NotificationHandler.showError(this.message);break;
            default: NotificationHandler.showInfo(this.message);break;
        }
    }

    /**
     * Affiche une notification toast.
     * @param {string} type - Type de message (success, error, warning, info).
     * @param {string} message - Message  afficher.
     */
    static showToast(type, message) {
        Swal.fire({
            toast: true,
            position: 'top-end',
            icon: type,
            title: message,
            showConfirmButton: false,
            timer: 3000,
            timerProgressBar: true,
        });
    }

    /**
     * Affiche une alerte de confirmation.
     * @param {string} title - Titre de l'alerte.
     * @param {string} text - Texte explicatif.
     * @param {Function} onConfirm - Callback si l'utilisateur confirme.
     */
    static confirmAction(title, text, onConfirm) {
        Swal.fire({
            title: title,
            text: text,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Oui, confirmer',
            cancelButtonText: 'Annuler',
        }).then((result) => {
            if (result.isConfirmed && typeof onConfirm === 'function') {
                onConfirm();
            }
        });
    }


      /**
     * Affiche une bote de confirmation pour une action critique.
     * @param {string} title - Titre de la bote de confirmation.
     * @param {string} text - Texte dcrivant les consquences de l'action.
     * @param {Function} onConfirm - Fonction  excuter si l'utilisateur confirme l'action.
     */
    //   static showConfirmation(title, text, onConfirm) {
    //     Swal.fire({
    //         title: title,
    //         text: text,
    //         icon: 'warning',
    //         showCancelButton: true,
    //         confirmButtonText: 'Oui, confirmer',
    //         cancelButtonText: 'Annuler',
    //         reverseButtons: true,
    //     }).then((result) => {
    //         if (result.isConfirmed) {
    //             // Excute la fonction de confirmation
    //             onConfirm();
    //         }
    //     });
    // }

    

    /**
     * Affiche une alerte avec un message personnalis.
     * @param {string} type - Type de message (success, error, warning, info).
     * @param {string} title - Titre du message.
     * @param {string} text - Texte dtaill du message.
     */
    static showAlert(type, title, text) {
        Swal.fire({
            icon: type,
            title: title,
            text: text,
            confirmButtonText: 'OK',
        });
    }

    /**
     * Affiche un message d'erreur gnrique.
     * @param {string} [message='Une erreur s\'est produite.'] - Message d'erreur  afficher.
     */
    static showError(message = 'Une erreur s\'est produite.') {
        NotificationHandler.showAlert('error', 'Une erreur s\'est produite', message);
    }

    /**
     * Affiche un message de succs gnrique.
     * @param {string} [message='Opration ralise avec succs.'] - Message de succs  afficher.
     */
    static showSuccess(message = 'Opration ralise avec succs.') {
        NotificationHandler.showToast('success', message);
    }

    /**
     * Affiche un message d'information gnrique.
     * @param {string} [message='Action en cours...'] - Message d'information  afficher.
     */
    static showInfo(message = 'Action en cours...') {
        NotificationHandler.showToast('info', message);
    }
}
