export class ContextStateService {
    /**
     * Constructeur pour initialiser ContextStateService.
     * @param {Object} initialState - L'tat initial du contexte.
     * @param {String} prefix - Prfixe pour les paramtres.
     */
    constructor(contextState = {}, prefix = 'context_') {
        this.contextState = contextState;
        this.prefix = prefix; // Prfixe pour les paramtres.
    }

    getVariables(){
        return this.contextState.variables;
    }

    /**
     * Met  jour le contexte d'tat.
     * @param {Object} newState - Les nouvelles variables  ajouter ou remplacer dans le contexte.
     */
    updateContext(newState) {
        this.contextState = { ...this.contextState, ...newState };
    }

    /**
     * Rcuprer les paramtres de contexte sous forme prfixe.
     * @returns {String} - Paramtres avec prfixe sous forme de chane.
     */
    getContextParams() {
        const prefixedContext = {};
        Object.entries(this.contextState.variables).forEach(([key, value]) => {
            prefixedContext[`${this.prefix}${key}`] = value;
        });
        return new URLSearchParams(prefixedContext).toString();
    }


       /**
     * Ajoute le contexte  l'objet config, y compris dans les URLs, et le retourne.
     * @param {Object} config - L'objet de configuration  modifier.
     * @returns {Object} - L'objet de configuration avec les paramtres de contexte ajouts.
     */
    addContextToConfig(config) {
        // Clone l'objet de configuration pour viter les modifications directes
        const updatedConfig = config ;

        // Prparer les paramtres de contexte sous forme de chane
        let contextParams;
        const prefixedContext = {};
        Object.entries(this.contextState).forEach(([key, value]) => {
            prefixedContext[`${this.prefix}${key}`] = value;
        });
        contextParams = new URLSearchParams(prefixedContext).toString();
    

        // Ajouter les paramtres de contexte aux URLs
        Object.keys(updatedConfig).forEach((key) => {
            if (key.toLowerCase().endsWith('url') && typeof updatedConfig[key] === 'string') {
                const url = new URL(updatedConfig[key], window.location.origin);
                const separator = url.search ? '&' : '?';
                updatedConfig[key] = `${url.toString()}${separator}${contextParams}`;
            }
        });

        return updatedConfig;
    }
}
