import { AjaxErrorHandler } from '../components/AjaxErrorHandler';
import { LoadingIndicator } from '../components/LoadingIndicator';
import { NotificationHandler } from '../components/NotificationHandler';
import { BaseAction } from './BaseAction';

export class LoadListAction extends BaseAction {
    /**
     * Constructeur de LoadListAction.
     * @param {Object} config - Configuration pour le chargement des entits.
     */
    constructor(config, tableUI) {

        super(config);
        
        this.config = config;
        this.tableUI = tableUI;

    
        this.indexUrl = this.appendParamsToUrl(
            config.indexUrl,
            this.contextService.getContextParams()
        );
    }

    /**
     * Charge les entits depuis le serveur et met  jour la table ou la liste.
     * @param {number} page - Numro de la page  charger (par dfaut : 1).
     * @param {Object} filters - Objets contenant les filtres actifs.
     */
    loadEntities(page = 1, filters = {}) {


         // Filtrer les filtres pour exclure les champs avec des valeurs vides
        const cleanedFilters = Object.fromEntries(
            Object.entries(filters).filter(([key, value]) => value !== null && value !== undefined && value !== '')
        );
        
        // Rcuprer les paramtres actuels depuis l'URL
        const urlParams = new URLSearchParams(window.location.search);
    
        const v = Object.fromEntries(urlParams.entries());
        // Intgrer les paramtres existants de l'URL et les nouveaux filtres
        const searchParams = { ...Object.fromEntries(urlParams.entries()), ...cleanedFilters };
        if(searchParams.page === undefined) {
            searchParams.page = page;
        }
        // Gnrer la chane de requte
        const queryString = new URLSearchParams(searchParams).toString();
    
        // Construire l'URL finale
        const requestUrl = this.appendParamsToUrl(this.indexUrl, queryString);
    
        // Afficher l'indicateur de chargement
        this.loader.show();
    
        // Requte AJAX pour charger les donnes
        $.get(requestUrl)
            .done((html) => {
                // Mettre  jour le conteneur avec les nouvelles donnes
                $(this.config.tableSelector).html(html);
    
                // Afficher un message de succs (optionnel)
                // NotificationHandler.showSuccess('Donnes charges avec succs.');
            })
            .fail((xhr) => {
                AjaxErrorHandler.handleError(xhr, "Erreur lors du chargement des donnes.");
            })
            .always(() => {
                // Masquer l'indicateur de chargement
                this.loader.hide();
            });
    }
    
}
