import { AjaxErrorHandler } from '../components/AjaxErrorHandler';
import { Action } from './Action';

export class ShowAction extends Action {


    constructor(config, tableUI) {
        super(config); 
        this.config = config;
        this.tableUI = tableUI; 
       
    }


    init(){
        this.handleShowEntity()
    }
    /**
     * Affiche les dtails d'une entit dans un modal.
     * @param {number|string} id - Identifiant de l'entit  afficher.
     */
    showEntity(id) {


        let showUrl = this.getUrlWithId(this.config.showUrl, id); // Gnrer l'URL dynamique

        showUrl = this.appendParamsToUrl(
            showUrl,
            this.contextService.getContextParams()
        );



        // Afficher le chargement dans le modal
        this.tableUI.indexUI.modalUI.showLoading('Dtails de l\'entit');

        // Charger les dtails de l'entit via AJAX
        $.get(showUrl)
            .done((html) => {
                // Injecter le contenu des dtails dans le modal


                this.tableUI.indexUI.modalUI.showContent(html);

                this.executeScripts(html);
                this.tableUI.indexUI.formUI.init();
                this.tableUI.indexUI.formUI.setToReadOnly();


                // $(document).on('opened', this.tableUI.indexUI.modalUI.this.currentModalId, function (e) {
                  
                // });

                // Excuter les scripts inclus dans le contenu AJAX
             
                // this.handleSuccess('Dtails de l\'entit chargs avec succs.');
            })
            .fail((xhr) => {
                this.tableUI.indexUI.formUI.modalManager.close();
                AjaxErrorHandler.handleError(xhr, 'Erreur lors du chargement des dtails de l\'entit.');
            });
            
    }


    /**
     * Gre les vnements lis  l'affichage des dtails d'une entit.
     */
    handleShowEntity() {
        $(document).on('click', `${this.config.crudSelector} .showEntity`, (e) => {
            e.preventDefault();
            const id = $(e.currentTarget).data('id'); // Rcuprer l'ID de l'entit
            this.showEntity(id);
        });
    }


        /**
     * Excute les scripts inclus dans le contenu HTML charg via AJAX.
     * @param {string} html - Contenu HTML contenant potentiellement des scripts.
     */
        executeScripts(html) {
            const scriptTags = $("<div>").html(html).find("script");
    
            scriptTags.each(function () {
                const scriptText = $(this).text();
                const scriptSrc = $(this).attr("src");
    
                if (scriptSrc) {
                    // Charger et excuter les scripts externes
                    $.getScript(scriptSrc);
                } else if (scriptText) {
                    // Excuter les scripts inline
                    new Function(scriptText)();
                }
            });
        }
}
