import { TableUI } from './TableUI';

export class FilterUI {

    constructor(config, indexUI) {
        this.config = config;
        this.indexUI = indexUI;
        // Temps de dlai pour limiter les requtes frquentes
        this.debounceTimeout = null;
        this.debounceDelay = 500; // Par dfaut : 500ms
    }

    init() {
        this.handleFormInput(); // Grer les entres dans le formulaire (recherche + filtres)
        this.adapterPourContext(); // Masquer les filtres dynamiquement selon le contexte
        this.initializeFilterResetHandler();
    }




    /**
     * Rcupre les valeurs de tous les champs du formulaire (recherche + filtres).
     * @returns {Object} - Un objet contenant les donnes du formulaire.
     */
    getFormData(withEmpty = false) {
        const form = $(this.config.filterFormSelector);
        const formData = {};

        // Parcourir les champs du formulaire
        form.serializeArray().forEach((field) => {
            const value = field.value.trim(); // Supprimer les espaces inutiles
            if (withEmpty || value) { // Inclure les champs vides seulement si demand
                formData[field.name] = value;
            }
        });

        return formData;
    }

    /**
     * Soumet le formulaire en rcuprant les donnes et recharge les entits.
     * @param {number} page - Numro de la page  charger (par dfaut : 1).
     */
    submitForm(page = 1) {
        const formData = this.getFormData(true); // Rcuprer les donnes du formulaire
        formData.page = page; // Ajouter le numro de page aux donnes
    

        // Mettre  jour l'URL avec les paramtres non vides
        this.indexUI.updateURLParameters(formData);
    
        // Charger les entits avec les paramtres
        this.indexUI.tableUI.entityLoader.loadEntities(page, formData);
    }

    /**
     * Gre les vnements du formulaire (recherche et filtres) avec dlai (debounce).
     */
    handleFormInput() {
        const formSelector = this.config.filterFormSelector;

        // Sur saisie dans les champs de recherche ou filtres
        $(document).on('input', `${formSelector} input, ${formSelector} select`, () => {
            clearTimeout(this.debounceTimeout);
            this.debounceTimeout = setTimeout(() => {
                this.submitForm(); // Soumettre le formulaire aprs un dlai
            }, this.debounceDelay);
        });

        // Soumission explicite du formulaire (par bouton ou utilisateur)
        $(document).on('submit', formSelector, (e) => {
            e.preventDefault();
            this.submitForm();
        });
    }


    /**
     * Masque les lments <select> dont l'id correspond  une cl dans le contexte (state).
     */
    adapterPourContext() {
        const contextState = this.config.contextStateService.getVariables();

        Object.keys(contextState).forEach((key) => {
            const filterElement = document.querySelector(`${this.config.filterFormSelector} #${key}`);
            if (filterElement) {
                if (this.config.isDebug) {
                    filterElement.parentElement.style.backgroundColor = 'lightblue'; // Mode debug : surligner
                } else {
                    filterElement.parentElement.style.display = 'none'; // Masquer le filtre
                }
            }
        });
    }


    initializeFilterResetHandler() {
        const filterIcon = document.querySelector(this.config.filterIconSelector);
        const filterForm = document.querySelector(this.config.filterFormSelector);
    
        // Fonction pour vrifier l'tat des filtres
        const updateFilterState = () => {
            const filters = new FormData(filterForm);
            let hasActiveFilters = false;
    
            for (let [key, value] of filters.entries()) {
                if (value.trim() !== '' && key !== 'page') {
                    hasActiveFilters = true;
                    break;
                }
            }
    
            if (hasActiveFilters) {
                filterIcon.classList.remove('fa-filter');
                filterIcon.classList.add('fa-times-circle');
            } else {
                filterIcon.classList.remove('fa-times-circle');
                filterIcon.classList.add('fa-filter');
            }
        };
    
        // Rinitialiser les filtres au clic sur l'icne
        filterIcon.addEventListener('click', () => {
            filterForm.querySelectorAll('input, select').forEach((field) => {
                field.value = ''; // Rinitialiser les champs
            });

             // Rinitialiser Select2
            $(filterForm).find('.select2').val(null).trigger('change');


            updateFilterState();
            this.submitForm(); // Soumettre le formulaire aprs rinitialisation
        });
    
        // Vrifier l'tat des filtres au chargement et sur modification
        updateFilterState();
        filterForm.addEventListener('input', updateFilterState);
    }



}
